<template lang="">
    <div v-infinite-scroll="load" class="infinite-list" style="overflow: auto">
        <el-card class="box-card infinite-list-item" v-for="(item,index) in dataList.slice((currentPage4-1)*pageSize4,currentPage4*pageSize4)" :key="index">
    <template #header>
      <div class="card-header">
        <h1  class="blog-title" @click="hangleTitle(index)">{{item.title}}</h1>
        <!-- <div class="blog-time">{{item.time}}</div> -->
      </div>
    </template>
    <!-- <div class="text item" v-html="item.contant"></div> -->
  </el-card>
  <el-pagination background layout="total, sizes, prev, pager, next, jumper" :total="dataList.length" :page-size="pageSize4" :page-sizes="[5, 10, 20]" :pager-count="5" :currentPage="currentPage4" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </div>

</template>
<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
const router = useRouter()

let dataList = ref([])
const currentPage4 = ref(1)
const pageSize4 = ref(10)
const count = ref(0)
const load = () => {
  count.value += 2
}
const hangleTitle = index => {
  router.push(`/blog/${index}`)
}

// 每页条数发生改变
const handleSizeChange = val => {
  pageSize4.value = val
  console.log(pageSize4.value)
}
// 页数发生改变
const handleCurrentChange = val => {
  currentPage4.value = val
  console.log(currentPage4.value)
}

onMounted(() => {
  dataList.value = JSON.parse(localStorage.getItem('blogList'))
  console.log(dataList.value)
})
</script>
<style lang="scss">
.card-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  .blog-title {
    cursor: pointer;
  }
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.box-card {
  width: 100%;
}
.infinite-list {
  height: 300px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.infinite-list .infinite-list-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  background: var(--el-color-primary-light-9);
  margin: 10px;
  color: var(--el-color-primary);
}
.infinite-list .infinite-list-item + .list-item {
  margin-top: 10px;
}
</style>