<template>
  <div>
    <div class="common-layout">
      <el-container>
        <div class="arc-left">
          <el-aside width="220px" :class="{ dark: isDark }">
            <div class="arc-title">
              <p>个人博客</p>
              <button class="switch" @click="toggleDark()">切换主题</button>
            </div>
            <el-row class="tac">
              <el-col>
                <!-- <h5 class="mb-2">Custom colors</h5> -->
                <el-menu active-text-color="black" background-color="#fff" class="el-menu-vertical-demo" default-active="2" text-color="black" :router="true">
                  <el-menu-item index="/"><el-icon>
                      <House />
                    </el-icon>主页</el-menu-item>
                  <el-menu-item index="/docs"><el-icon>
                      <DocumentCopy />
                    </el-icon>添加博客</el-menu-item>
                  <el-menu-item index="/archives"><el-icon>
                      <Folder />
                    </el-icon>文章列表</el-menu-item>
                  <el-menu-item @click="handleDialog" index=''><el-icon>
                      <Search />
                    </el-icon>搜索</el-menu-item>
                </el-menu>
              </el-col>
            </el-row>
          </el-aside>
        </div>
        <el-main><router-view></router-view></el-main>
      </el-container>
      <el-dialog v-model="dialogVisible" title="搜索文章" width="50%">
        <el-input v-model="input2" class="w-50 m-2" placeholder="请搜索文章标题" :prefix-icon="Search" @input="handleInput" />
        <ul :key="refreshKey">
          <li class="itemSearch" v-for="(item,index) in filteredData" :key="item.time" @click="handleLi(index,item.title)">
            {{item.title}}
          </li>
        </ul>
      </el-dialog>
    </div>
  </div>
</template>
<script  setup>
import { House, Search, Folder, DocumentCopy } from '@element-plus/icons-vue'
// import { useTheme } from '../../router/util/useTheme.ts'
import { ref, onMounted, watch } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessageBox } from 'element-plus'
// const { theme } = useTheme()
import { useDark, useToggle } from '@vueuse/core'

const isDark = useDark()
const toggleDark = useToggle(isDark)
// const isDarkMode = ref(false)
let textColor = ref('')
let colorScheme = ref('')
let dataList = ref([])
const router = useRouter()
const input2 = ref('')
const filteredData = ref([])
const refreshKey = ref(0)

onMounted(() => {
  dataList.value = JSON.parse(localStorage.getItem('blogList'))
  console.log(dataList.value)
  console.log(isDark)
  console.log(toggleDark())
})

// 开关弹框
const dialogVisible = ref(false)
const handleDialog = () => {
  dialogVisible.value = !dialogVisible.value
}
// 点击搜索结果跳转
const handleLi = (i, str) => {
  dialogVisible.value = !dialogVisible.value
  for (let index = 0; index < dataList.value.length; index++) {
    if (dataList.value[index].title === str) {
      console.log(index)
      router.push(`/blog/${index}`).then(() => {
        window.location.reload()
      })

      break // 可以添加跳出循环的语句，提高效率
    }
  }
}
// 搜索匹配
const handleInput = () => {
  filteredData.value = dataList.value.filter(item => item.title.includes(input2.value))
  if (input2.value == '') {
    filteredData.value = ''
  }
  console.log(filteredData.value)
}
</script>
<style lang="scss">
.arc-left {
  border: 1px solid #ccc !important;
  margin-left: 50px;
  height: 330px;
  .el-aside {
    .arc-title {
      height: 100px;
      color: #fff;
      background-color: #222222;
      position: relative;
      p {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
      }
      .switch {
        position: absolute;
        padding: 3px;
        border-radius: 5px;
        background-color: aqua;
        cursor: pointer;
        color: black;
        top: 10px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}
.el-main {
  margin-left: 10px;
  margin-right: 40px;
  background-color: #fff;
  border: 1px solid #ccc;
}
.el-dialog {
  height: 70%;
  overflow-y: auto;
  ul {
    .itemSearch {
      cursor: pointer;
      line-height: 30px;
      height: 30px;
    }
  }
}
</style>